<template>
  <div class="js-container">
    <div class="article-list">
      <ul>
          <li v-for="(item,index) in aritcleList" :key="index">
            <router-link to="/homePage/article">
              <Card class="article-info" title="true">
                <p slot="title">{{item.title}}</p>
                <p>{{item.content}}</p>
              </Card>
            </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import VueMarkDown from 'vue-markdown'
export default {
  components: {
    VueMarkDown
  },
  data () {
    return {
      aritcleList: []
    }
  },
  methods: {
    test: function () {
      var _this = this
      this.$axios.post('/jsPage', {page: 1}).then(res => {
        res.data.forEach(function (item) {
          _this.aritcleList.push({
            title: item.title,
            content: item.content,
            url: item.url
          })
        })
        var article = require(_this.aritcleList[0].url)
        console.log(article)
      })
    }
  },
  mounted () {
    this.test()
  }
}
</script>

<style>
.article-info {
  width: 600px;
  height: 300px;
  margin: 30px auto;
}
</style>



